import React from "react";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Register from "./pages/auth/register";
import Login from "./pages/auth/login";

import PPDB from "./layout/ppdb";
import Identitas from "./layout/identitas"
import store from "./redux/store";
import { Provider } from "react-redux";
import { ChakraProvider, theme } from "@chakra-ui/react";
import {SiswaPageProtected, AuthProtected} from "./layout/ProtetedRoute"



function App() {
 

  

  return (
    <Provider store={store}>
      <BrowserRouter>
      <ChakraProvider resetCSS={true} theme={theme}>
        <Switch>
          <AuthProtected path="/login">
            <Login></Login>
          </AuthProtected>
         
          <AuthProtected path="/register">
            <Register></Register>
          </AuthProtected>
          <SiswaPageProtected path="/admin">
            <p>admin</p>
          </SiswaPageProtected>
          <SiswaPageProtected path="/ppdb">
            <PPDB></PPDB>
          </SiswaPageProtected>
          <SiswaPageProtected path="/identitas">
            <Identitas></Identitas>
          </SiswaPageProtected>
          <Redirect from="/" to="/login" />
          <Redirect from="*" to="/login" />
        </Switch>
        </ChakraProvider>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
